

/**
 * ==============
 * Design variables
 * ==============
 */
:root{
  --contents-max-width: 600px;
  --nav-height: 20px;
  --gutter: 20px;
  --main-font-family: sans-serif;
  --main-color: #345feaff;
  --secondary-color: #193aa3;

  --color-type-1-type-1: #02c39a;
  --color-type-1-type-2: #00a896;
  --color-type-1-type-3: #028090;
  --color-type-1-type-4: #05668d;

  --color-type-2-type-1: #bc3908; 
  --color-type-2-type-2: #941b0c;
  --color-type-2-type-3: #621708;
  --color-type-2-type-4: #220901;
}

/**
 * ==============
 * Layout
 * ==============
 */
 nav{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--nav-height);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  padding: 0;
  background: white;
  z-index: 2;
  height: var(--nav-height);
}

footer{
  padding-top: var(--gutter);
}
footer > div{
  max-width: var(--contents-max-width);
  min-width: var(--contents-max-width);
}

main{
  position: relative;
  top: calc(var(--nav-height) * 2);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
}
main > div{
  max-width: var(--contents-max-width);
  min-width: var(--contents-max-width);
}

@media (max-width: 600px) {
  main > div{
    max-width: 100vw;
    min-width: 100vw;
    padding: var(--gutter);
    box-sizing: border-box;
  }
}

.share-container {
  transition: all .5s ease;
  max-height: 1000vh;
  overflow: hidden;
}

.share-container.hidden{
  max-height: 0;
}

.question-container{
  height: calc(100vh - var(--nav-height) * 2);
  max-height: calc(100vh - var(--nav-height) * 2);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.question-container > li.active{
  margin-bottom: var(--gutter);
}

.question-container > li:not(.active) {
  opacity: .5;
  margin-bottom: var(--gutter);
  overflow: hidden;
  cursor: pointer;
}

.question-container > li:not(.active) h2{
  font-size: .7em;
  margin: 0;
  margin-bottom: calc(var(--gutter) / 4);
}
.question-container > li:not(.active) textarea{
  font-size: .8em;
  pointer-events: none;
  min-height: unset;
}

.question-container > li h2,
.question-container > li textarea{
  transition: all .2s ease;
}

.question-container.normalized{
 max-height: 100vh;
 height: auto;
 justify-content: flex-start;
}

/**
 * ==============
 * Fonts settings
 * ==============
 */
body{
  font-family: var(--main-font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
input,
textarea{
  font-family: var(--main-font-family);
}


/**
 * ==============
 * Components
 * ==============
 */
/**
 * Button
**/
 button{
  background: none;
  border: none;
  color: black;
  background: lightgrey;
  font-size: 1em;
}

button:not([disabled]) {
  cursor: pointer;
}
button:hover:not([disabled]),
button.active:not([disabled])
{
   color: white;
   background: var(--main-color);
  background: grey;
 }
button[disabled]{
  border: none;
}

/**
 * Types of buttons
 */

 .important-button{
  width: 100%;
  padding: var(--gutter);
  background: var(--main-color);
  color: white;
  border: none;
}

.important-button[disabled] {
  opacity: .5;
}

.important-button:not([disabled]):hover{
  background: var(--secondary-color);
}

.important-link,
.important-link:visited
{
  text-decoration: none;
  background: var(--main-color);
  padding: var(--gutter);
  width: calc(100% - var(--gutter) * 2);
  color: white;
  margin-top: var(--gutter);
  display: block;
  text-align: center;
}
.important-link:hover{
  background: var(--secondary-color);
}

button.stop {
  background: var(--color-type-2-type-1);
  color: white;
}
button.stop:hover:not([disabled]),
button.stop.active:not([disabled])
{
  background: var(--color-type-2-type-2);

}
button.stop.important-button {
  background: var(--color-type-2-type-3);
}
button.important-button.stop:hover:not([disabled]),
button.important-button.stop.active:not([disabled])
{
  background: var(--color-type-2-type-4);

}

button.develop {
  background: var(--color-type-1-type-1);
  color :white;
}
button.develop:hover:not([disabled]),
button.develop.active:not([disabled])
{
  background: var(--color-type-1-type-2); 
}
button.develop.important-button {
  background: var(--color-type-1-type-3);
}
button.important-button.develop:hover:not([disabled]),
button.important-button.develop.active:not([disabled])
{
  background: var(--color-type-1-type-4); 
}

/**
 * Row of buttons
**/
.buttons-row{
  margin: 0;
  padding: 0;
  margin-top: var(--gutter);
  list-style: none;
  display: flex;
  flex-flow: row wrap;
}
.buttons-row.reverse{
  flex-direction : row-reverse;
}
.buttons-row li{
  flex: 1;
}
.buttons-row li button{
  width: 100%;
  padding: var(--gutter);
  height: 100%;
}
.buttons-row li:not(:last-of-type) button{
  border-right: none;
}

/**
 * Nav items
**/
nav ul{
  list-style: none;
  max-width: var(--contents-max-width);
  width: var(--contents-max-width);
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  position: relative;
  height: calc(var(--gutter) * 3);
  background: white;
}
@media (max-width: 500px) {
  nav ul{
    padding-left: var(--gutter);
  }
}

nav ul li a,
nav ul li a:visited{
  color: var(--main-color);
  text-decoration: none;
  padding: var(--gutter);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
nav ul li:first-of-type a {
  padding-left: 0;
}

nav ul li a:not(.home-link):hover{
  background: var(--main-color);
  color: white;
}

nav ul li a.active:not(.home-link){
  background: var(--main-color);
  color: white;
  cursor: pointer;
}
/* lang toggle */
nav ul li:last-of-type{
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  padding: var(--gutter);
  color: var(--main-color);
  padding-right: 0;
}
@media (max-width: 500px) {
  nav ul li:last-of-type{
    position: relative;
  }
}

/**
 * Big squares select
**/
.big-select{
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  overflow: hidden;
  transition: all .5s ease;
  max-height : 100vh;
}

.big-select.hidden{
  max-height: 0;
}


.big-select li{
  width: 50%;
  height: 100%;
}
.big-select li button{
  width: 100%;
  height: calc(var(--contents-max-width) / 2);
  font-size: 1.2rem;
}

.big-select li:not(:last-of-type) button{
  border-right: 1px solid white;
}

/**
 * textareas (for now only in questionnaire)
**/
textarea{
  width: 100%;
  border: none;
  max-height: 40vh;
  min-height: 20vh;
  resize: none;
  padding: 0;
  outline: none;
  font-size: 1em;
}

/**
 * inputs (for now only for email)
**/
input{
  width: 100%;
  border: 1px solid grey;
  padding: var(--gutter);
  margin-bottom: var(--gutter);
  box-sizing: border-box;
  outline: none;
  font-size: 1em;
}
input::placeholder{
  opacity: 1;
}
input:hover::placeholder,
input:focus::placeholder{
  opacity: .5;
}

.radio-container{
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch;
  padding: var(--gutter) 0;
  padding-top: 0;
}

.radio-container input{
  width: unset;
  margin-right: var(--gutter);
}
.radio-container label{
  flex: 1;
}

/**
 * Propositions review
 */
.proposition{
  padding: var(--gutter);
  background: rgba(0,0,0,0.05);
  margin-bottom: var(--gutter);
}
.proposition button{
  padding: calc(var(--gutter) / 2);
  padding: calc(var(--gutter) / 2);
  font-size: .7em;
}
.proposition h3{
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch;
  align-items: center;
}

.proposition h3 .proposition-title{
  flex: 1;
}
.proposition h3 .small-buttons-container button:not(:last-of-type){
  border-right: 1px solid white;
}
.proposition .question{
  padding: var(--gutter);
  background: rgba(0,0,0,0.05);
  margin-bottom: var(--gutter);
  cursor: pointer;
}
.proposition .question:hover{
  background: rgba(0,0,0,0.1);
}

.proposition blockquote{
  margin-left: 0;
}

/**
 * Highlighted section
 */
.important-section{
  padding: var(--gutter);
  margin-bottom: var(--gutter);
  margin-top: var(--gutter);
  background: rgba(0,0,0,0.05);
}


/**
 * xhr status overlay
**/
.status-container{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  z-index: 2;
  align-items: center;
}

.status-content{
  background: black;
  color: white;
  padding: 5vmin;
  text-align: center;
}

.padded-container{
  padding-left: var(--gutter);
  padding-right: var(--gutter);
}

.text-length-indicator{
  font-size: 0.8em;
  margin-top: var(--gutter);
}